.page-top{
    padding-top: 1.5em;
}

.breadcrumbs {
    &__item {
        position: relative;
        display: inline;
        font-size: .875em;
        line-height: 130%;
        letter-spacing: .01em;
        color: $grayDark;
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: -.03125em;
                right: 100%;
                margin-right: .25em;
                color: $grayDark;
            }
        }
        &:last-child{
            font-weight: 400;
            pointer-events: none;
            color: $black;
        }
        &:hover{
            //color: $darkBlue;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding-top: 0;
    }
}


@media screen and (max-width: 576px){
    .page-top{
        &._hide-mob{
            display: none;
        }
    }
}
