.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #000;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 35em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .75em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: .875em;
    }
    .close-btn{
        width: 1.5em;
        @include center;
        flex-shrink: 0;
        margin-left: auto;
        svg{
            width: 100%;
        }
    }
    &__img{
        display: block;
        width: 9.0625em;
        margin: 0 auto 2em;
    }
    &__text{
        @include grid100gap(.75em);
        text-align: center;
    }
    &__title{
        font-size: 1.5em;
        line-height: 140%;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: .005em;
    }
    &__desc{
        font-size: 1em;
        line-height: 140%;
        letter-spacing: .01em;
        color: $dark;
    }
    &__btn{
        margin-top: 1.5em;
        width: 100%;
        font-weight: 600;
    }
    .logo{
        display: none;
    }
}

.phone-modal{
    &__field{
        font-weight: 500;
        margin-top: 2.5em;
        height: 3.75em;
        @include center;
        border: 1px solid $gray;
        border-radius: .75em;
        font-family: $font2;
    }
    &__nav{
        margin-top: 1em;
        .m-btn{
            font-weight: 500;
            height: 3.125em;
            &:not(:hover){
                background-color: $dark;
            }
            span{
                font-size: 1em;
            }
            img{
                width: 1.3125em;
                margin-right: .75em;
            }
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__dialog {
            height: $vh;
            overflow-y: auto;
            max-width: 100%;
            border-radius: 0;
            padding: 1.5em 1em;
        }
        &:after{
            display: none;
        }
        &__top{
            margin-bottom: 3em;
        }
        .logo{
            display: block;
            width: 9.375em;
        }
        &__img{
            width: 11.25em;
            margin-bottom: 2.5em;
        }
        &__text{
            gap: .5em;
        }
    }

    .phone-modal{
        &__nav{
            gap: .75em;
        }
    }
}
