.main{
  padding: 2em 0 5em;
}

.main-slider{
  overflow: hidden;
  border-radius: .75em;
  position: relative;
  &__nav{
    position: absolute;
    bottom: 2em;
    left: 0;
    width: 100%;
    @include center;
  }
  .swiper{
    &-pagination{
      width: auto;
      position: static;
      padding: 0 1.125em;
      &-bullet{
        background-color: transparent;
        border: 2px solid $white;
        width: 1em;
        height: 1em;
        margin: 0 .375em;
        opacity: 1;
        &-active{
          background-color: $white;
        }
      }
    }
    &-button{
      width: 2em;
      height: 2em;
      position: static;
    }
  }
}

.swiper{
  &-nav{
    display: flex;
    align-items: center;
    .swiper-button{
      position: static;
      margin-left: 3em;
    }
  }
  &-button{
    margin: 0;
    width: 2.5em;
    height: 2.5em;
    @include center;
    svg{
      width: 100%;
    }
  }
}

.main-banner{
  border-radius: .75em;
  background: $grad1;
  padding: .75em 5em 2.375em 5em;
  color: $white;
  display: grid;
  grid-template-columns: 50em calc(100% - 54.5em);
  justify-content: space-between;
  align-items: center;
  &__content{
    padding: 3em 0;
  }
  &__img{
    max-width: 40.4375em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 38.375em;
      height: 38.375em;
      border-radius: 50%;
      background: linear-gradient(180deg, #fff 0%, rgba(255, 255, 255, 0.2) 100%);
      top: 2.25em;
      left: 50%;
      transform: translateX(-50%);
      opacity: .3;
    }
    img{
      position: relative;
      z-index: 2;
      width: 100%;
      display: block;
    }
  }
  @media screen and (min-width: 993px){
    .h1{
      font-size: 2.25em;
    }
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 45em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
    .m-btn{
      padding: 0 5em;
    }
  }
}

.multi-slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      padding-right: 1.25em;
    }
    &-scrollbar{
      display: none;
    }
  }
}

.brands-slider{
  margin-top: 2em;
  .swiper{
    &-slide{
      width: calc(100% / 6);
    }
  }
}

.brand-card{
  &__img{
    @include imgCover(51.85%);
  }
}


@media screen and (max-width: 1400px){
  .main{
    padding-bottom: 4.375em;
  }

  .swiper{
    &-button{
      width: 2em;
      height: 2em;
    }
  }

  .main-slider{
    &__nav{
      bottom: 1.5em;
    }
    .swiper{
      &-pagination{
        &-bullet{
          width: .75em;
          height: .75em;
        }
      }
      &-button{
        width: 1.5em;
        height: 1.5em;
      }
    }
  }

  .main-banner{
    padding: 2em 2.5em 1.5em 3em;
    grid-template-columns: 35em calc(100% - 35em - 2.625em);
    &__content{
      padding: 0;
    }
    &__nav{
      .m-btn{
        padding: 0 4em;
      }
    }
    &__img{
      max-width: 40.4375em;
      position: relative;
      &:before{
        width: 30em;
        height: 30em;
        top: .5em;
      }
      img{
        position: relative;
        z-index: 2;
        width: 100%;
        display: block;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: .5em 0 2.5em;
  }

  .main-slider{
    .swiper{
      &-pagination{
        &-bullet{
          width: .5em;
          height: .5em;
          border-width: 1px;
        }
      }
      &-button{
        display: none;
      }
    }
  }

  .main-banner{
    grid-template-columns: 100%;
    padding: 1.5em 1.5em 3em;
    &__img{
      display: none;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .multi-slider{
    .swiper{
      &-scrollbar{
        display: block;
        margin-top: 1em;
        height: 2px;
        background-color: $grayWhite;
        border-radius: 4px;
        &-drag{
          background-color: $dark;
          border-radius: inherit;
          cursor: pointer;
          &:before{
            z-index: 3;
          }
        }
      }
    }
    .swiper-scrollbar,
    .swiper-scrollbar-drag{
      &:before{
        content: '';
        position: absolute;
        height: 300%;
        width: 100%;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .brands-slider{
    .swiper{
      &-slide{
        width: 25%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-banner{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .brands-slider{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }
}
