@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/filter-slider";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    height: 2em;
    @include lastM(0 1em 0 0);
    @include center;
    &:hover{
      path{
        fill: $black;
      }
      circle{
        stroke: $black;
      }
    }
    &:active{
      path{
        fill: $red;
      }
      circle{
        stroke: $red;
      }
    }
    svg{
      width: 100%;
    }
  }
}

.cart-btn,
.favourite-btn{
  @include center;
  width: 2em;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  &:not(._added):hover{
    path{
      stroke: $dark;
    }
  }
  &._added{
    path{
      stroke: $red;
      fill: $red;
    }
  }
}

.cart-btn{
  &:hover{
    path{
      stroke: $dark;
    }
  }
  &._added{
    path{
      stroke: $red;
    }
  }
  &__title{
    display: none;
  }
}



