.about{
  &__img{
    img{
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      display: block;
      border-radius: .75em;
    }
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__items{
    margin-top: 3em;
  }
}

.about-item{
  padding: 1.5em;
  border-radius: .75em;
  border: 2px solid $yellow;
  .h2{
    color: $dark;
  }
  &__desc{
    margin-top: .5em;
    color: $grayDark;
    span{
      font-size: 1.25em;
      line-height: 150%;
      letter-spacing: .01em;
    }
  }
}


@media screen and (max-width: 1400px){
  .about{
    &__desc{
      margin-top: 2em;
      .text20{
        font-size: 1em;
      }
    }
    &__items{
      margin-top: 2em;
    }
  }

  .about-item{
    padding: 1em;
    .h2{
      font-size: 1.5em;
    }
    &__desc{
      span{
        font-size: .875em;
        line-height: 120%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
    &__img{
      order: -1;
    }
    &__desc{
      margin-top: 1em;
      .text20{
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__img{
      img{
        min-height: 12.5em;
      }
    }
    &__items{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .about-item{
    padding: 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__desc{
      span{
        font-size: 1em;
      }
    }
  }
}
