@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 3.375em;
    border-radius: .75em;
    font-weight: 500;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 110%;
    }
    &--big{
        text-transform: uppercase;
        padding: 0 5em;
        span{
            font-size: 1em;
        }
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: darken($white,10%);
        color: $black;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        background-color: darken($black,10%);
        color: $white;
    }
}

.text-btn{
    &:hover{
        color: $dark;
    }
    &:active{
        color: $red;
    }
}


@media screen and (max-width: 1400px){
    .m-btn{
        height: 3em;
        span{
            font-size: 1.125em;
        }
        &--big{
            height: 3.375em;
        }
    }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.625em;
        span{
            font-size: 1em;
        }
        &--big{
            height: 2.75em;
        }
    }
}

