.return{
  &__top{
    max-width: 71.25em;
    @include grid100gap(1em);
  }
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
    color: $dark;
  }
  &__items{
    margin-top: 2em;
  }
  &__hint{
    margin-top: 2em;
    max-width: 68.375em;
  }
}

.return-step{
  padding: 2.5em 2em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    margin-right: 1em;
  }
}

.return-info{
  &__groups{
    @include grid100gap(5em);
    max-width: 81.875em;
  }
  &__text{
    margin-top: 1.5em;
    @include grid100gap(.75em);
    color: $dark;
    p{
      font-size: 1.125em;
      line-height: 150%;
      letter-spacing: .01em;
    }
  }
}


@media screen and (max-width: 1400px){
  .return{
    &__desc{
      display: none;
    }
    &__hint{
      max-width: 58em;
    }
  }

  .return-step{
    padding: 2em 1.5em;
    &__top{
      margin-bottom: 1em;
      align-items: flex-start;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .return{
    &__hint{
      .text16{
        line-height: 150%;
      }
    }
  }

  .return-step{
    padding: 1.5em;
    &__top{
      margin-bottom: .75em;
    }
  }

  .return-info{
    &__text{
      margin-top: 1em;
      p{
        font-size: 1em;
      }
    }
  }
}


