.payment{
  &__items{
    margin-top: 2em;
  }
}

.delivery-card,
.payment-card{
  padding: 1.5em;
  border-radius: .75em;
  label{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    border: 1px solid transparent;
    border-radius: inherit;
  }
  input{
    z-index: 3;
    &:checked~label{
      border-color: $grayDark;
    }
  }
}

.payment-card{
  border: 1px solid $gray;
  &__icon{
    height: 4em;
    margin-bottom: 1em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 140%;
    letter-spacing: .01em;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .5em;
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    color: $grayDark;
  }
}

.delivery-info{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 45em) 35em;
    justify-content: space-between;
  }
  &__top{
    @include grid100gap(1em);
  }
  &__desc{
    font-size: 1.25em;
    line-height: 150%;
    letter-spacing: .01em;
    color: $dark;
  }
  &__items{
    margin-top: 3em;
    @include grid100gap(2em);
  }
}

.delivery-item{
  @include grid100gap(.75em);
  &__title{
    font-size: 1.5em;
    line-height: 110%;
    letter-spacing: .01em;
    font-weight: 500;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 150%;
    color: $dark;
  }
}

.delivery-banner{
  padding: 0 1.875em 2em;
  border-radius: .75em;
  background: $grad1;
  color: #fff;
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__bottom{
    padding: 2.5em 1.5em 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-size: 2em;
    line-height: 110%;
    font-weight: 600;
    letter-spacing: 0.01em;
  }
  &__btn{
    margin-top: 1.5em;
    height: 3.125em;
    padding: 0 5em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 1400px){
  .payment-card{
    &__icon{
      height: 3em;
    }
    &__title{
      font-size: 1.125em;
    }
  }

  .delivery-info{
    &__grid{
      grid-template-columns: calc(100% - 24.5625em - 1.25em) 24.5625em;
    }
    &__desc{
      font-size: 1.125em;
    }
    &__items{
      gap: 1.5em;
    }
  }

  .delivery-item{
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1em;
      line-height: 160%;
    }
  }

  .delivery-banner{
    padding: 0 .25em 1.5em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__bottom{
      padding-top: 1.25em;
    }
    &__title{
      font-size: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .payment{
    &__items{
      margin-top: 1em;
    }
  }

  .payment-card{
    &__icon{
      display: none;
    }
    &__desc{
      max-width: 15em;
    }
  }

  .delivery-info{
    &__grid{
      grid-template-columns: 100%;
      gap: 5em;
    }
    &__desc{
      font-size: 1em;
    }
    &__items{
      gap: 2em;
    }
  }

  .delivery-banner{
    padding: 0 1.5em 1.5em;
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .payment{
    &__items{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .delivery-banner{
    &__bottom{
      padding-top: .5em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__btn{
      height: 2.6875em;
    }
  }
}
