.error404{
  padding-top: 10em;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img{
    width: 36.5625em;
    display: block;
    max-width: 100%;
    margin-bottom: 1.5em;
  }
  &__text{
    font-size: 1.25em;
    line-height: 140%;
    letter-spacing: .01em;
    color: $grayDark;
  }
  &__btn{
    padding: 0 5em;
    margin-top: 2.5em;
    text-transform: uppercase;
    height: 3.375em;
    span{
      font-size: 1em;
    }
  }
}



@media screen and (max-width: 1400px){
  .error404{
    padding-top: 8.75em;
  }
}



@media screen and (max-width: 992px){
  .error404{
    padding-top: 5em;
    text-align: center;
    &__img{
      margin-bottom: 1em;
    }
    &__text{
      font-size: 1em;
    }
    &__btn{
      height: 2.75em;
      text-transform: unset;
      margin-top: 2em;
      span{
        font-size: 1.125em;
      }
    }
  }
}



@media screen and (max-width: 576px){
  .error404{
    &__btn{
      width: 100%;
    }
  }
}
