.m-section{
  padding: 5em 0;
  &.m-section--first{
    padding-top: 2em;
  }
}

.product-slider{
  margin-top: 2em;
  position: relative;
  &__slide{
    width: 25%;
  }
  .swiper{
    &-nav{
      margin-bottom: 2.25em;
      position: absolute;
      right: 0;
      bottom: 100%;
    }
  }
}

.product-card{
  position: relative;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 993px){
    .swiper-pagination{
      opacity: 0;
    }
    .product-card__buttons{
      opacity: 0;
      transition: all .3s;
    }
  }
  &:hover{
    .product-card__buttons,
    .swiper-pagination{
      opacity: 1;
    }
  }
  &__slider{
    overflow: hidden;
    position: relative;
    border-radius: .75em;
    .swiper{
      &-pagination{
        bottom: .75em;
        @include center;
        transition: all .3s;
        &-bullet{
          margin: 0 .25em !important;
          width: .5em;
          height: .5em;
          background-color: transparent;
          border: 1px solid $grayDark;
          opacity: 1;
          &-active{
            background-color: $grayDark;
          }
        }
      }
    }
  }
  &__pagination{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    display: flex;
    &-item{
      height: 100%;
      width: auto;
      flex-grow: 1;
    }
  }
  &__img{
    @include imgCover(82%);
    border-radius: .75em;
  }
  &__badges{
    left: 1.5em;
  }
  &__badges,
  &__buttons{
    position: absolute;
    z-index: 3;
    top: 1.5em;
  }
  &__buttons{
    right: 1.5em;
    display: flex;
    align-items: center;
  }
  .cart-btn{
    margin-left: 1em;
  }
  &__content{
    margin-top: 1.25em;
  }
  &__bottom{
    margin-top: .75em;
  }
  &__price{
    font-weight: 600;
    margin-right: .75em;
    span{
      font-size: 1.5em;
      line-height: 150%;
    }
  }
  &__price2{
    color: $grayDark;
    display: flex;
    align-items: center;
    span{
      text-decoration: line-through;
      font-size: 1.125em;
      line-height: 150%;
    }
  }
  &__sale{
    margin-left: .75em;
    @include center;
    padding: 0 .5em;
    height: 1.75em;
    background-color: $yellowDark;
    font-weight: 500;
    border-radius: .5em;
    color: #fff;
    span{
      font-size: .875em;
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.m-badge{
  background-color: $green;
  padding: 0 .75em;
  height: 1.875em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  font-weight: 500;
  color: $white;
  span{
    font-size: 1em;
    line-height: 140%;
  }
  &._red{
    background-color: #F15D5D;
  }
  &._blue{
    background-color: #5D7E;
  }
}


@media screen and (max-width: 1400px){
  .m-section{
    padding: 4.375em 0;
  }

  .product-slider{
    &__slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2.5em 0;
  }

  .product-slider{
    margin-top: 1em;
    .multi-slider__wrap{
      width: calc(100% + 2em);
      margin-left: -1em;
      padding: 1em;
      border-radius: 1em;
      background-color: $grayWhiteWhite;
    }
    .swiper-nav{
      display: none;
    }
  }

  .product-card{
    &__pagination{
      display: none;
    }
    &__img{
      border: $border2;
    }
    &__badges{
      top: .75em;
      left: .75em;
    }
    .favourite-btn{
      top: .75em;
      right: .75em;
      position: absolute;
    }
    &__buttons{
      order: 2;
      position: static;
      margin-top: auto;
    }
    .cart-btn{
      margin: 0;
      align-self: flex-start;
      position: static;
      padding: 0 1.25em;
      height: 2.0625em;
      background-color: $dark;
      color: $white;
      width: auto;
      border-radius: .5em;
      span{
        font-size: .75em;
        display: block;
      }
      svg{
        display: none;
      }
    }
    &__content{
      margin: .5em 0 1em;
    }
    &__title{
      font-size: .875em;
    }
    &__price{
      margin-right: .5em;
      span{
        font-size: .875em;
        line-height: 110%;
      }
    }
    &__price2{
      span{
        font-size: .625em;
        line-height: 110%;
      }
    }
    &__sale{
      margin-left: .5em;
      padding: 0;
      height: unset;
      background-color: transparent;
      font-weight: 600;
      border-radius: 0;
      color: $red;
      span{
        font-size: .75em;
        line-height: 110%;
      }
    }
  }

  .m-badge{
    height: 1.375em;
    span{
      font-size: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .product-slider{
    &__slide{
      width: 50%;
    }
  }
}
