.fg {
  position: relative;
  margin: 0;
  select,
  input,
  textarea{
    font-family: $font1;
    border-radius: 0.75em;
    padding: 0 1.5em;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    border: $border2;
    color: $black;
    &:focus{
      border-color: $black;
    }
    &::placeholder{
      color: $grayDark;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  label{
    font-size: .875em;
    line-height: 140%;
    letter-spacing: .01em;
    display: block;
    color: $grayDark;
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select-arrow.svg);
    background-repeat: no-repeat;
    background-size: 1.125em auto;
    background-position: right 1em center;
    &::-ms-expand {
      display: none; // IE
    }
  }
  &._small{
    select,
    input{
      height: 2.875em;
      border-radius: .5em;
    }
  }
}

.color-item,
.size-item,
.delivery-card,
.payment-card,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.75em;
    min-height: 1.5em;
    span{
      font-size: 1em;
      line-height: 140%;
      letter-spacing: .01em;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $grayDark;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $dark;
        border-color: $dark;
        background-size: 90% auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 8rem;
  height: 2.5em;
  position: relative;
  input{
    border: none;
    background-color: #fff;
    height: 100%;
    width: 100%;
    font-size: 1em;
    line-height: 140%;
    text-align: center;
    padding: 0 2.5em;
    border-radius: 1.5625em;
    letter-spacing: 0.01em;
  }
  button{
    position: absolute;
    @include center;
    width: 1.25em;
    height: 1.25em;
    top: 50%;
    transform: translateY(-50%);
    &:hover{
      path{
        stroke: $black;
      }
    }
    &:active{
      path{
        stroke: $red;
      }
    }
  }
  svg{
    width: 100%;
  }
  .ccalc-minus{
    left: 1em;
  }
  .ccalc-plus{
    right: 1em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px) {
  .fg {
    input {
      height: 2.875em;
    }
  }
}
