.product-items{
  margin-top: 2em;
}

.catalog{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2em;
  }
}

.filter{
  &__btn,
  &__bg,
  &__top{
    display: none;
  }
  &__items{
    display: flex;
    align-items: center;
  }
}

.m-dropdown{
  .m-check{
    @include lastM(0 0 1em);
    label{
      display: flex;
      align-items: center;
      padding-left: 1.875em;
      &:before{
        width: 1.125em;
        height: 1.125em;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    i{
      width: .875em;
      height: .875em;
      flex-shrink: 0;
      border-radius: .25em;
      margin-right: .75em;
    }
  }
}

.filter-item{
  @include lastM(0 2.5em 0 0);
}

.filter-price{
  .filter-item__hidden{
    min-width: 20em;
  }
  .filter-item__inner{
    padding: 0;
    overflow: unset;
  }
}

.catalog-sort{
  .m-dropdown__hidden{
    left: unset;
    right: 0;
  }
  .m-dropdown__inner{
    max-height: 9.25em;
  }
}

.filter-mob{
  display: none;
}


@media screen and (max-width: 992px){
  .product-items{
    margin: 1em 0 0 -1em;
    width: calc(100% + 2em);
    padding: 1em;
    background-color: $grayWhiteWhite;
    border-radius: 1em;
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    height: $vh;
    background-color: #fff;
    width: 24em;
    max-width: 100%;
    z-index: 30;
    overflow-y: auto;
    transition: all .3s;
    transform: translateX(-200%);
    &__bg{
      background: #000;
      opacity: .4;
      position: fixed;
      z-index: 29;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      overflow-y: auto;
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1em 1em;
      height: 4.5em;
    }
    &__items{
      display: flex;
      align-items: center;
    }
    &__bottom{
      padding: 1.5em 1em;
    }
    &__items{
      display: block;
    }
    &__close{
      width: 1.5em;
      @include center;
      svg{
        width: 100%;
      }
    }
    &__btn{
      margin-top: 1.5em;
      display: flex;
      width: 100%;
      border-radius: .5em;
      text-transform: uppercase;
      span{
        font-size: .875em;
      }
    }
  }

  .filter-item{
    @include lastM(0 0 2.5em);
    &__show{
      justify-content: space-between;
      font-weight: 600;
      text-transform: uppercase;
      color: $black;
    }
    &__arrow{
      width: 1.125em;
      path{
        stroke: $black;
      }
    }
    &__hidden{
      min-width: 100% !important;
      transition: unset;
      position: static;
      opacity: 1;
      pointer-events: unset;
      border: none;
      padding: 0;
      margin-top: 1.5em;
      display: none;
    }
    &__inner{
      overflow-y: unset;
      max-height: unset;
    }
  }

  .filter-sizes{
    .filter-item__inner{
      flex-wrap: wrap;
      flex-direction: unset;
      margin-top: -1em;
      width: calc(100% + 1em);
    }
    .m-check{
      margin: 1em 1em 0 0 !important;
      label{
        padding: 0 1em;
        height: 2.5em;
        min-width: 4em;
        justify-content: center;
        border-radius: .5em;
        background-color: $grayWhiteWhite;
        &:before{
          display: none;
        }
      }
      input:checked~label{
        background-color: $black;
        color: $white;
      }
    }
  }

  .filter-mob{
    display: flex;
    align-items: center;
    color: $grayDark;
    svg{
      width: 1.25em;
      margin-left: .5em;
    }
  }

  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
  }
}


