.pagination{
  margin-top: 5em;
  &__items{
    display: flex;
    justify-content: center;
  }
  &__item{
    height: 2.22em;
    min-width: 2.22em;
    padding: 0 .25em;
    border-radius: 0.44em;
    border: $border2;
    @include center;
    margin: 0 .22em;
    font-weight: 500;
    font-size: 1.125em;
    color: $grayDark;
    svg{
      width: 1.33em;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $dark;
      border-color: $dark;
      path{
        fill: #fff;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .pagination{
    margin-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .pagination{
    margin-top: 1em;
    ._hide-tablet{
      display: none;
    }
  }
}
