.catalog-item{
  padding-top: 2em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 36.25em) 35em;
    gap: 1.5em 1.25em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: .01em;
    font-weight: 500;
    text-transform: uppercase;
  }
}

.gallery{
  overflow: hidden;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset;
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .625em));
        gap: 1.25em;
      }
      &-slide{
        width: 100% !important;
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.gallery-item{
  &__img{
    @include imgCover(125%);
    border-radius: .75em;
    display: block;
  }
}

.catalog-info{
  &__wrap{
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price{
    font-size: 1.75em;
    line-height: 140%;
    font-weight: 700;
    color: $red;
  }
  &__price2{
    font-size: 1em;
    line-height: 140%;
    color: $grayDark;
    margin-left: .75em;
    text-decoration: line-through;
  }
  &__btn{
    width: 2em;
    height: 2em;
    @include center;
    margin-left: 1.5em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $black;
      }
    }
    &:active{
      path{
        stroke: $red;
      }
    }
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    .m-btn{
      padding: 0;
      width: 100%;
    }
  }
  &__buy{
    padding: .25em 0;
    color: $grayDark;
    margin-top: 1em;
    border-bottom: 1px solid $grayDark;
    span{
      font-size: 1em;
      line-height: 140%;
      letter-spacing: .01em;
    }
  }
}

.installment{
  display: inline-flex;
  align-items: center;
  margin-top: .25em;
  color: $dark;
  &:hover{
    color: $red;
    path{
      fill: $red;
    }
  }
  svg{
    width: .875em;
    margin-left: .25em;
  }
  &__title{
    font-size: .875em;
    line-height: 140%;
    b{
      font-weight: 600;
    }
  }
}

.size{
  margin-top: 2em;
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
}

.size-item{
  margin: .5em .75em 0 0;
  label{
    transition: all .3s;
    padding: 0 .5em;
    @include center;
    height: 1.75em;
    border-radius: .25em;
    background-color: $grayWhite;
    span{
      font-size: .875em;
      letter-spacing: .01em;
    }
  }
  input:checked~label{
    background-color: $dark;
    color: $white;
  }
}

.colors{
  margin-top: 1em;
  &__items{
    margin-top: .5em;
    display: flex;
  }
}

.color-item{
  margin-right: .75em;
  input:checked{
    &~label{
      border-color: #000;
    }
  }
  label{
    border: 1px solid transparent;
    border-radius: .25em;
    width: 2em;
    height: 2em;
    @include center;
    padding: .0625em;
    transition: all .3s;
    span{
      width: 100%;
      height: 100%;
      border-radius: inherit;
    }
  }
}

.catalog-detail{
  margin-top: 3em;
}

.info-card{
  padding: 1.5em 0;
  border-top: $border2;
  &._open{
    .info-card__btn{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &:last-child{
    border-bottom: $border2;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 140%;
    letter-spacing: .01em;
  }
  &__btn{
    width: 1.5em;
    margin-left: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    margin-top: 1em;
    display: none;
  }
  &__props{
    @include grid100gap(.75em);
  }
  &__delivery{
    b{
      font-weight: 600;
    }
  }
  .product-items{
    margin-top: 0;
  }
  .prop-item{
    &__desc{
      white-space: unset;
      max-width: 50%;
    }
  }
}


@media screen and (max-width: 1400px){
  .catalog-item{
    padding-top: 2em;
    &__grid{
      display: grid;
      grid-template-columns: calc(100% - 24.5625em - 1.25em) 24.5625em;
      gap: 1.25em;
    }
  }

  .catalog-info{
    &__price{
      font-size: 1.5em;
    }
  }

  .info-card{
    @media screen and (min-width: 993px){
      .product-card{
        &__title{
          font-size: 1em;
        }
        &__badges{
          top: 1em;
          left: .75em;
          .m-badge{
            font-size: .875em;
          }
        }
        .favourite-btn{
          top: .75em;
          right: .75em;
          position: absolute;
        }
        &__buttons{
          order: 2;
          position: static;
          margin-top: auto;
        }
        &__content{
          margin: .5em 0 1em;
        }
        &__price{
          margin-right: .5em;
          span{
            font-size: 1em;
            line-height: 110%;
          }
        }
        &__price2{
          span{
            font-size: .75em;
            line-height: 110%;
          }
        }
        &__sale{
          height: 1.5625em;
          span{
            font-size: .75em;
          }
        }
        .cart-btn{
          margin: 0;
          align-self: flex-start;
          position: static;
          padding: 0 1.25em;
          height: 2.0625em;
          background-color: $dark;
          color: $white;
          width: auto;
          border-radius: .5em;
          span{
            font-size: .75em;
            display: block;
          }
          svg{
            display: none;
          }
        }

      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .gallery{
    position: sticky;
    top: 1.25em;
    .swiper{
      &-pagination{
        bottom: 0;
        padding: .75em .625em;
        @include center;
        &-bullet{
          width: auto;
          height: 2px;
          flex-grow: 1;
          border-radius: .25em;
          opacity: 1;
          background-color: $grayDark;
          &-active{
            background-color: $white;
          }
        }
      }
    }
  }

  .catalog-info{
    &__price2{
      font-size: .875em;
      margin-left: .9em;
    }
  }

  .info-card{
    padding: 1em 0;
    &__title{
      font-size: 1.125em;
    }
    &__btn{
      width: 1.25em;
    }
    .prop-item{
      &__title,
      &__desc{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding-top: 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__title{
      font-size: 1.25em;
    }
  }

  .gallery{
    width: calc(100% + 2em);
    margin-left: -1em;
  }
}
