.logo{
  display: block;
  flex-shrink: 0;
  width: 12.3125em;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 1.5em 0 0;
  position: relative;
  z-index: 20;
  &__inner{
    display: flex;
    align-items: flex-end;
    position: relative;
    padding-bottom: 3.5em;
  }
  &__logo{
    margin-right: 3em;
  }
  &._open-search{
    .header-search{
      opacity: 1;
      pointer-events: unset;
      &__btn{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }
}

.burger{
  display: none;
}

.nav,
.nav2{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 2.5em 0 0);
    span{
      font-size: 1em;
      line-height: 140%;
      letter-spacing: .01em;
    }
  }
}

.nav{
  &__link {
    color: $grayDark;
  }
}

.nav2{
  position: absolute;
  left: 0;
  bottom: 0;
  &__link {
    font-weight: 500;
    &:hover{
      color: $red;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  bottom: 0;
  &__items{
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    margin-left: 2em;
    &:hover{
      path:not(._fill){
        stroke: $dark;
      }
      ._fill{
        fill: $dark;
      }
    }
    &:active{
      path:not(._fill){
        stroke: $red;
      }
      ._fill{
        fill: $red;
      }
    }
    &--hide-desktop{
      display: none;
    }
    span{
      display: none;
    }
    svg{
      width: 2em;
    }
  }
}

.header-search{
  position: absolute;
  top: 100%;
  right: 100%;
  width: 32em;
  margin-right: -4em;
  margin-top: 1em;
  transition: all .3s;
  opacity: 0;
  &__btn{
    position: relative;
    width: 2em;
    height: 2em;
    svg{
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      width: 1.5em;
      opacity: 0;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .75em;
    width: 1.125em;
  }
  input{
    background-color: $white;
    padding-left: 2.25em;
  }
}

.header-contacts{
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: flex-end;
  &__socials{
    display: flex;
    align-items: center;
    .contact-item{
      margin-left: 1em;
    }
  }
  &__links{
    display: flex;
    @media screen and (min-width: 1401px){
      .contact-item{
        &__icon{
          display: none;
        }
        span{
          display: block;
        }
      }
    }
  }
}

.contact-item{
  margin-left: 2em;
  color: $grayDark;
  display: flex;
  align-items: center;
  &__link{
    &:hover{
      color: $black;
      .contact-item__icon{
        path{
          fill: $black;
        }
        circle{
          stroke: $black;
        }
      }
    }
    &:active{
      color: $red;
      .contact-item__icon{
        path{
          fill: $red;
        }
        circle{
          stroke: $red;
        }
      }
    }
  }
  &__link{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 2em;
  }
  span{
    display: none;
  }
  .m-dropdown{
    &__hidden{
      right: 0;
      left: unset;
      min-width: 15em;
    }
  }
}

.filter-item,
.m-dropdown{
  position: relative;
  &._open{
    .filter-item__hidden,
    .m-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
    .filter-item__arrow,
    .m-dropdown__arrow{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    color: $grayDark;
    &:hover{
      cursor: pointer;
      color: $black;
      path{
        stroke: $black;
      }
    }
    &:active{
      color: $red;
      path{
        stroke: $red;
      }
    }
  }
  &__arrow{
    width: 1.25em;
    margin-left: .5em;
  }
  &__hidden{
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    margin-top: .75em;
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 18em;
    border: 1px solid #000;
    padding: 1.5em .75em 1.5em 1.5em;
    background-color: $white;
    border-radius: .75em;
  }
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-right: 1em;
    max-height: 16.625em;
    overflow-y: auto;
    @include scroll;
  }
  &__link{
    font-size: 1em;
    line-height: 140%;
    @include lastM(0 0 1em);
  }
}


@media screen and (max-width: 1400px){
  .header{
    &__inner{
      padding-top: .3125em;
    }
    &__logo{
      width: 11.6875em;
    }
  }

  .nav{
    &__link{
      @media screen and (min-width: 993px){
        @include lastM(0 1.5em 0 0);
        span{
          font-size: .875em;
        }
      }
    }
  }

  .contact-item{
    margin-left: 1em;
    @media screen and (min-width: 993px){
      .m-dropdown{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    .container{
      padding: 0;
    }
    &__inner{
      padding: 0;
      justify-content: space-between;
    }
    &__mob{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.5em 1em;
      background-color: $white;
      position: relative;
      z-index: 11;
    }
    &__logo{
      width: 9.625em;
      z-index: 11;
      position: relative;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    display: flex;
    flex-direction: column;
    padding: 5em 1em 6.25em;
    overflow-y: auto;
  }

  .nav{
    order: 4;
  }

  .nav2{
    position: static;
    order: 3;
    margin-bottom: 1em;
  }

  .nav,
  .nav2{
    flex-direction: column;
    &__link{
      width: 100%;
      @include lastM(0 0 1em);
      border-bottom: $border;
      padding-bottom: .625em;
      color: $dark;
      font-weight: 500;
    }
  }

  .header-action{
    position: static;
    order: 2;
    &__items{
      z-index: 11;
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      background-color: $dark;
      border-radius: .5em .5em 0 0;
      padding: 1em 1em 1.5em;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    }
    &__btn{
      margin: 0;
      min-width: 6em;
      flex-direction: column;
      font-weight: 300;
      color: $gray;
      &--hide-mob{
        display: none;
      }
      &--hide-desktop{
        display: flex;
      }
      path:not(._fill){
        stroke: $gray;
      }
      ._fill{
        fill: $gray;
      }
      svg{
        height: 1.5em;
        width: auto;
        margin-bottom: .25em;
      }
      span{
        display: block;
        font-size: .75em;
        line-height: 140%;
        letter-spacing: 0.02em;
      }
      &--active{
        font-weight: 500;
        color: $yellow;
        path:not(._fill){
          stroke: $yellow;
        }
        ._fill{
          fill: $yellow;
        }
      }
    }
    &__search{
      order: 2;
      width: 100%;
    }
  }

  .header-search{
    width: 100%;
    margin: 0 0 3em;
    position: relative;
    top: unset;
    transform: unset;
    right: unset;
    opacity: 1;
    pointer-events: unset;
  }

  .header-contacts{
    position: static;
    order: -1;
    width: 100%;
    margin-bottom: 2em;
    flex-direction: column;
    &__socials{
      width: 100%;
      justify-content: space-between;
      .contact-item{
        margin: 0;
      }
    }
    &__links{
      border-bottom: $border2;
      padding-bottom: .75em;
      margin-bottom: .75em;
      order: -1;
      width: 100%;
      justify-content: space-between;
    }
  }

  .contact-item{
    margin: 0;
    &:first-child{
      position: relative;
      .m-dropdown{
        position: static;
        &__hidden{
          right: unset;
          left: 0;
        }
      }
    }
    &__icon{
      display: none;
    }
    span{
      display: block;
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .header-search{
    input{
      padding: 0 1em 0 3em;
      font-size: .875em;
      height: 3.29em;
      border-radius: .57em;
    }
  }
}
