.cart-empty{
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__img{
    width: 22.5em;
    display: block;
  }
  &__text{
    margin-top: 4em;
    @include grid100gap(.5em);
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__link{
    padding: 0 5em;
    font-weight: 400;
  }
}

.cart{
  &__count{
    margin-top: 5em;
  }
  &__grid{
    margin-top: 1em;
    display: grid;
    grid-template-columns: calc(100% - 25.9375em - 1.25em) 25.9375em;
    gap: 5em 1.25em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 1em;
    display: flex;
  }
  &__clear{
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    color: $grayDark;
    text-transform: uppercase;
  }
}

.cart-detail{
  padding: 1.5em;
  background-color: $grayWhiteWhite;
  border-radius: .75em;
  position: sticky;
  top: 1.25em;
  &__props{
    @include grid100gap(1em);
  }
  &__sum{
    font-weight: 500;
    display: flex;
    justify-content: space-between;
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border2;
    text-transform: uppercase;
    &-title,
    &-desc{
      font-size: 1.125em;
      line-height: 140%;
      letter-spacing: .01em;
    }
  }
  &__nav{
    margin-top: 2.5em;
  }
  &__btn{
    height: 3em;
    width: 100%;
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title{
    color: #8c8c8c;
  }
  &__desc{
    color: #505050;
    text-align: right;
    white-space: nowrap;
  }
}

.cart-card{
  padding: 1.5em 1em;
  border-radius: .75em;
  background-color: $grayWhiteWhite;
  display: grid;
  grid-template-columns: 7.5em calc(100% - 35em) 16em 7em;
  gap: 1.5em;
  position: relative;
  width: calc(100% - 2.5em);
  margin-left: auto;
  &__check{
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translate(-1em,-50%);
    label{
      width: 1.5em;
      padding: 0;
    }
  }
  &__img{
    @include imgCover(130%);
    border-radius: .5em;
    border: $border2;
  }
  &__content{
    padding-top: .6875em;
  }
  &__title{
    text-transform: uppercase;
    span{
      font-size: 1.125em;
      line-height: 140%;
      letter-spacing: .01em;
    }
  }
  &__props{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
  &__colors{
    display: flex;
    margin-left: .375em;
  }
  &__color{
    width: .9375em;
    height: .9375em;
    border-radius: 50%;
  }
  &__right{
    display: flex;
    flex-direction: column;
  }
  &__price,
  &__price2{
    line-height: 140%;
    letter-spacing: .01em;
    white-space: nowrap;
  }
  &__price{
    font-size: 1.5em;
  }
  &__price2{
    font-size: .875em;
    text-decoration: line-through;
    color: $grayDark;
  }
  &__action{
    margin-top: auto;
    padding-top: 2em;
  }
  &__delete{
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    font-weight: 600;
    color: $grayDark;
    text-transform: uppercase;
    &:hover{
      color: $red;
    }
  }
}


@media screen and (max-width: 1400px){
  .cart-empty{
    &__link{
      padding: 0 4em;
      height: 3em;
      span{
        font-size: 1.125em;
      }
    }
  }

  .cart{
    &__count{
      margin-top: 2em;
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: calc(100% - 18.125em - 1.25em) 18.125em;
    }
  }

  .cart-detail{
    padding: 1.5em 1em;
    &__sum {
      margin-top: 1.25em;
      padding-top: 1.25em;
    }
  }

  .cart-card{
    padding: 1em;
    grid-template-columns: 7.5em calc(100% - 28.125em) 9.125em 7em;
  }
}


@media screen and (max-width: 992px){
  .cart-empty{
    &__text{
      margin-top: 2em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__link{
      padding: 0 5.75em;
      height: 2.625em;
      span{
        font-size: 1em;
      }
    }
  }

  .cart{
    &__count{
      margin-top: 3em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-detail{
    position: static;
  }
}


@media screen and (max-width: 576px){
  .cart-empty{
    &__img{
      width: 13.25em;
    }
  }

  .cart-card{
    padding: 1em 1em 1.5em;
    grid-template-columns: 100%;
    width: 100%;
    &__img{
      width: 4em;
      height: 5.1875em;
      padding: 0;
    }
    &__content{
      padding: 0;
    }
    &__title{
      position: absolute;
      top: 1em;
      left: 6em;
      width: calc(100% - 8em);
      span{
        font-size: 1em;
      }
    }
    &__props{
      margin: -.5em 0 0;
    }
    &__right{
      margin-top: 1em;
      position: relative;
    }
    &__prices{
      position: absolute;
      top: -5em;
      right: 0;
    }
    &__action{
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__check{
      position: relative;
      right: unset;
      top: unset;
      transform: unset;
    }
  }
}
