.original{
  &__inner{
    padding: 5em 9em;
    border-radius: .75em;
    background-color: $yellow;
    background-image: url(../img/original-bg.svg);
    background-size: cover;
    background-position: center;
    text-align: center;
  }
  &__text{
    width: 60.625em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(1.5em);
  }
  &__info{
    padding: 1.5em;
    margin-top: 2em;
    border-radius: .75em;
    background-color: $white;
    .text24{
      letter-spacing: .01em;
    }
  }
}

.how-check{
  &__grid{
    margin-top: 2em;
  }
}

.m-num{
  flex-shrink: 0;
  width: 2.5em;
  height: 2.5em;
  background-color: #1EBC24;
  border-radius: 50%;
  @include center;
  color: $white;
  span{
    font-size: 1.5em;
  }
}

.check-step,
.return-step{
  border-radius: .75em;
  background-color: $grayWhiteWhite;
  &__top{
    margin-bottom: .75em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 130%;
    letter-spacing: .01em;
    font-weight: 600;
  }
}

.check-step{
  padding: 1em 3.25em 1em 5.5em;
  display: grid;
  grid-template-columns: calc(100% - 14.625em - 2.5em) 14.625em;
  justify-content: space-between;
  position: relative;
  min-height: 14.25em;
  &._type2{
    grid-template-columns: 100%;
  }
  &__content{
    padding-top: 1.5em;
  }
  &__num{
    position: absolute;
    top: 2.5em;
    left: 2em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
  }
  &__honest{
    width: 19.75em;
    display: block;
  }
  .stores{
    margin-left: auto;
    margin-right: -.875em;
  }
}

.stores{
  display: flex;
  align-items: center;
  &__item{
    margin-left: 1.5em;
    height: 3em;
    img{
      height: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 1400px){
  .original{
    &__inner{
      padding: 3em 7.875em;
    }
    &__info{
      width: calc(100% - 2.5em);
      margin: 1.5em auto 0;
    }
  }

  .m-num{
    width: 2em;
    height: 2em;
    span{
      font-size: 1.25em;
    }
  }

  .check-step{
    padding: 2em 1em 2em 4.5em;
    grid-template-columns: calc(100% - 10.5em) 10em;
    gap: .5em;
    min-height: 12.5em;
    &__content{
      padding: 0;
    }
    &__title{
      font-size: 1.25em;
    }
    &__top{
      margin-bottom: .6875em;
    }
    &__num{
      top: 2em;
      left: 1.5em;
    }
    &__bottom{
      margin-top: 1.6875em;
    }
    &__honest{
      width: 13.5625em;
    }
    .stores{
      margin-right: 0;
    }
  }

  .stores{
    &__item{
      margin-left: .875em;
      height: 2.1875em;
    }
  }
}


@media screen and (max-width: 992px){
  .original{
    &__inner{
      padding: 2em 1.5em;
    }
    &__text{
      gap: 1em;
    }
    &__info{
      margin-top: 2em;
      padding: 1em;
      font-weight: 500;
      width: 100%;
    }
  }

  .m-num{
    width: 1.75em;
    height: 1.75em;
    span{
      font-size: 1em;
    }
  }

  .check-step{
    grid-template-columns: 100%;
    padding: 1.5em;
    &__top{
      display: flex;
      align-items: flex-start;
    }
    &__num{
      position: static;
      margin-right: .75em;
    }
    &__img{
      display: none;
    }
    &__bottom{
      margin-top: 1.4375em;
      display: block;
    }
    &__honest{
      max-width: 20em;
      width: 100%;
    }
    .stores{
      margin: 1.5em 0 0;
    }
  }

  .stores{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    &__item{
      margin: 0;
      img{
        height: auto;
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .original{
    &__inner{
      background-image: url(../img/original-bg2.svg);
    }
  }

  .how-check{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}
