.honest{
  &__inner{
    padding: 3em 19.75em 0;
    background: linear-gradient(225deg, #fff385 2.97%, rgba(255, 240, 186, 0.3) 100%);
    display: grid;
    grid-template-columns: 18.75em calc(100% - 21.75em);
    gap: 3em;
    align-items: center;
    border-radius: .75em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__text{
    span{
      font-weight: 600;
    }
    .text24{
      line-height: 160%;
    }
  }
}

.sales{
  &__grid{
    margin-top: 2em;
  }
}

.sale-card{
  padding: 2em;
  border-radius: .75em;
  background-color: $grayWhiteWhite;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  overflow: hidden;
  &:hover{
    .sale-card__img img{
      transform: scale(1.05);
    }
  }
  &--type1{
    grid-column: span 2;
    height: 28em;
    .sale-card__img{
      padding: 2.25em 0;
      max-width: calc(100% - 11.5em);
    }
  }
  &--type2{
    height: calc(100% - 8em);
  }
  &--type3{
    grid-column: span 2;
    height: calc(100% + 8em);
    margin-top: -8em;
    background-image: url(../img/wave.svg);
    background-size: cover;
    background-position: center;
    .sale-card__img{
      padding: 1em 0 0;
      max-width: calc(100% - 21.5625em);
    }
  }
  &__title,
  &__price{
    position: relative;
    z-index: 2;
    font-weight: 500;
  }
  &__title{
    font-size: 1.5em;
    line-height: 150%;
  }
  &__price{
    margin-top: auto;
    padding: 0 1.25em;
    border-radius: .75em;
    background-color: $white;
    height: 2.875em;
    display: flex;
    align-items: center;
    span{
      font-size: 1.25em;
    }
  }
  &__img{
    position: absolute;
    height: 100%;
    max-width: calc(100% - 6em);
    bottom: 0;
    right: 0;
    display: flex;
    align-items: flex-end;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
      transition: all .6s;
    }
  }
}

.delivery{
  padding: 1.25em 4.125em 1.25em 2em;
  border-radius: .75em;
  background-color: $yellow;
  grid-column: span 2;
  display: grid;
  grid-template-columns: calc(100% - 17.5em - 5.375em) 17.5em;
  align-items: center;
  justify-content: space-between;
  &__title{
    font-size: 2em;
    line-height: 150%;
    font-weight: 600;
    letter-spacing: .01em;
  }
  &__desc{
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    margin-top: .75em;
    span{
      font-weight: 600;
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2em;
    .m-btn{
      padding: 0 2em;
    }
  }
}

.subscribe{
  &__inner{
    padding: 6.4375em;
    border-radius: .75em;
    background: $grad2;
    color: #fff;
  }
  &__text{
    text-align: center;
    @include grid100gap(.5em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 110%;
    letter-spacing: .01em;
  }
}

.subscribe-form{
  margin: 2.5em auto 0;
  width: 35em;
  max-width: 100%;
  .fg{
    input{
      color: #fff;
      &::placeholder{
        color: #fff;
      }
    }
  }
  &__btn{
    margin-top: 1.5em;
    width: 100%;
    height: 3.1875em;
    span{
      font-size: 1em;
    }
  }
  .m-check{
    margin-top: .625em;
    color: $grayWhiteWhite;
    padding-left: .125em;
    input {
      &:checked+label {
        &::before {
          background-color: $grayWhiteWhite;
          border-color: $grayWhiteWhite;
        }
      }
    }
    label{
      padding-left: 1.5em;
      &:before{
        width: .875em;
        height: .875em;
        border-radius: .125em;
        top: .1875em;
        border-color: $grayWhiteWhite;
        background-image: url(../img/check2.svg);
      }
    }
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 1400px){
  .honest{
    &__inner{
      padding: 1em 3em 0 7.875em;
      grid-template-columns: 16.25em calc(100% - 16.25em - 2.875em);
      gap: 1em 2.875em;
      align-items: unset;
    }
    &__text{
      padding-top: 3.375em;
    }
  }

  .sale-card{
    padding: 1.5em;
    &--type1{
      height: 19.75em;
    }
    &--type2{
      height: calc(100% - 5.625em);
    }
    &--type3{
      height: calc(100% + 5.625em);
      margin-top: -5.625em;
      .sale-card__img{
        padding: 1.5em 1.75em 0 0;
        max-width: calc(100% - 15.0625em);
      }
    }
    &__price{
      height: 2.5em;
      span{
        font-size: 1em;
      }
    }
  }

  .delivery{
    padding: .8125em 1.5em;
    grid-template-columns: calc(100% - 12.5em - 1.875em) 12.5em;
    &__title{
      font-size: 1.5em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .subscribe{
    .h1{
      font-size: 2em;
    }
    &__inner{
      padding: 3em 1.5em;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .subscribe-form{
    margin-top: 2em;
    width: 24.625em;
    &__btn{
      margin-top: 1em;
      height: 2.9375em;
    }
    .m-check{
      margin-top: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .honest{
    &__inner{
      padding: 1.5em 1.5em 0;
    }
  }

  .sales{
    &__grid{
      margin-top: 1em;
    }
  }

  .sale-card{
    &--type2{
      height: 18em;
    }
    &--type3{
      height: 19.75em;
      margin: 0;
    }
  }

  .delivery{
    order: 2;
  }

  .subscribe-form{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .honest{
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      order: 2;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      padding: 0;
    }
  }

  .sales{
    .h2{
      max-width: 80%;
    }
    &__grid{
      gap: .75em;
    }
  }

  .sale-card{
    padding: 1em;
    grid-column: 1/-1;
    height: 10.625em;
    &--type3{
      .sale-card__img{
        padding: 0;
        max-width: calc(100% - 7em);
      }
    }
    &__title{
      font-size: 1em;
    }
    &__price{
      height: 2.3125em;
      span{
        font-size: .875em;
      }
    }
  }

  .delivery{
    padding: 1.5em 1em;
    grid-template-columns: 100%;
    grid-column: 1/-1;
    &__img{
      display: none;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
      margin-top: .875em;
    }
    &__nav{
      display: none;
    }
  }

  .subscribe{
    .h1{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .subscribe-form{
    &__btn{
      margin-top: .75em;
      height: 2.6875em;
    }
  }
}
