.filter-slider{
  width: 100%;
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $grayWhite;
    border: none;
    box-shadow: none;
    height: .375em;
    border-radius: .125em;
    .noUi-connect{
      background-color: $black;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: transparent;
      background-image: url(../img/arrow.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: left bottom;
      width: 1.125em;
      height: 2.625em;
      top: -1.125em !important;
      right: -.5625em !important;
      border-radius: 0;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 1em;
    display: flex;
    justify-content: space-between;
    &-price,
    &-title{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &-title{
      left: 1em;
      color: $grayDark;
    }
    &-price{
      right: .75em;
    }
  }
  .fg{
    position: relative;
    max-width: 8.125em;
    input{
      height: 2.375em;
      color: $black;
      font-weight: 400;
      font-size: 1em;
      line-height: 140%;
      padding-left: 2.375em;
      border-radius: .5em;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: 1em;
      line-height: 140%;
      color: $grayDark;
    }
  }
}
