.footer{
  padding-top: 5em;
  a{
    &:hover{
      color: $black;
    }
  }
  &__inner{
    padding: 3em 0 1em;
    border-top: $border2;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__text,
  &__desc{
    font-size: 1em;
    line-height: 160%;
  }
  &__desc{
    margin-top: .75em;
    color: $dark;
  }
  &__text{
    color: $grayDark;
  }
  &__bottom{
    border-top: $border2;
    margin-top: 3em;
    padding-top: 1.5em;
    display: flex;
    justify-content: space-between;
  }
}

.footer-info{
  max-width: 22.75em;
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1.5em;
  &__link{
    margin-bottom: 1em;
    font-size: 1em;
    line-height: 160%;
    color: $dark;
  }
}

.footer-nav{
  flex-grow: 1;
  max-width: 71.25em;
  column-count: 4;
  column-gap: 1.25em;
  &__link{
    display: inline-block;
    width: 100%;
    font-size: 1em;
    line-height: 140%;
    letter-spacing: .01em;
    margin-bottom: 1em;
    color: $dark;
  }
}


@media screen and (max-width: 1400px){
  .footer{
    padding-top: 4.375em;
    &__inner{
      padding-top: 2em;
    }
    &__bottom{
      margin-top: 2em;
      padding-top: 1.25em;
    }
    &__text{
      font-size: .875em;
      line-height: 120%;
    }
  }

  .footer-info{
    max-width: 17.9375em;
  }

  .footer-nav{
    max-width: 50.5em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 2.5em;
    &__inner{
      padding-top: 1.5em;
    }
    &__bottom{
      margin-top: 1.5em;
    }
  }

  .footer-nav{
    column-count: 2;
    max-width: 26em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      display: block;
    }
    &__bottom{
      flex-direction: column;
      align-items: center;
    }
    &__text{
      @include lastM(0 0 1.14em);
    }
  }

  .footer-nav{
    border-top: $border2;
    margin-top: 1.5em;
    padding-top: 1.5em;
    column-count: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      font-weight: 500;
      margin-bottom: 1.25em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}
