.search{
  &__title{
    font-weight: 400;
  }
  &__variants{
    margin: 0 auto;
    max-width: 71.25em;
    width: 100%;
  }
  &__links{
    margin-top: 1.5em;
    column-count: 3;
    column-gap: 3em;
    width: 56.625em;
    max-width: 100%;
  }
  &__link{
    font-size: 1.125em;
    line-height: 120%;
    letter-spacing: .01em;
    color: #8c8c8c;
    display: inline-block;
    width: 100%;
    margin-bottom: .67em;
  }
}

.search-top{
  max-width: 71.25em;
  width: 100%;
  margin: 0 auto 5em;
  &__title{
    text-transform: uppercase;
    text-align: center;
    font-size: 3em;
    line-height: 110%;
    letter-spacing: .01em;
  }
}

.search-form{
  display: flex;
  margin-top: 3em;
  &._filled{
    .search-form__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .fg{
    flex-grow: 1;
    input{
      padding-left: 2.625em;
    }
  }
  &__icon,
  &__clear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.125em;
  }
  &__icon{
    left: 1em;
  }
  &__clear{
    right: 1em;
    opacity: 0;
    pointer-events: none;
    svg{
      width: 100%;
    }
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1.25em;
    text-transform: uppercase;
    width: 16.875em;
    height: 3.375em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 1400px){
  .search{
    &__title{
      font-size: 1.25em;
    }
    &__variants{
      max-width: 63.375em;
    }
    &__links{
      column-gap: 1.25em;
    }
    &__link{
      font-size: 1em;
      margin-bottom: .75em;
    }
  }

  .search-top{
    max-width: 63.375em;
    &__title{
      font-size: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .search{
    &__links{
      column-count: 2;
    }
  }

  .search-top{
    margin-bottom: 2em;
  }

  .search-form{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .search{
    &__title{
      font-size: 1.125em;
    }
    &__links{
      column-count: 1;
    }
  }

  .search-top{
    &__title{
      font-size: 1.5em;
    }
  }

  .search-form{
    display: block;
    .fg{
      input{
        height: 3.375em;
        border-radius: .5em;
      }
    }
    &__btn{
      margin: 1.5em 0 0;
      width: 100%;
      height: 2.75em;
      text-transform: unset;
      span{
        font-size: 1.125em;
      }
    }
  }
}
