.contacts{
  &__grid{
    display: grid;
    grid-template-columns: 44.0625em 53.125em;
    justify-content: space-between;
    gap: 5em 1.25em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .75em;
    }
  }
  &__address{
    margin-top: 5em;
    @include grid100gap(.5em);
    padding-bottom: 1.5em;
    margin-bottom: 1.5em;
    border-bottom: $border2;
  }
  &__items{
    @include grid100gap(1em);
  }
  &__item{
    display: grid;
    grid-template-columns: 16.875em calc(100% - 16.875em - 1.25em);
    gap: 1.25em;
    .text16{
      letter-spacing: .01em;
    }
  }
}


@media screen and (max-width: 1400px){
  .contacts{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    &__address{
      margin-top: 3em;
      padding-bottom: 1em;
      margin-bottom: 1em;
    }
    &__item{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-top: 2em;
    &__address{
      padding-bottom: 0;
      margin-bottom: 2em;
      border: none;
      max-width: 90%;
      .h3{
        font-size: 1.125em;
        font-weight: 500;
      }
    }
    &__item{
      grid-template-columns: 1fr 1.2fr;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
    }
  }
}
