.order{

}

.order-delivery,
.order-contacts,
.order-payment{
  margin-top: 7.5em;
}

.order-payment{
  .payment-card{
    border: none;
    background-color: $grayVeryWhite;
  }
  &__nav{
    display: flex;
    margin-top: 4em;
  }
}

.order-contacts{
  &__fields{
    margin-top: 2em;
  }
}

.delivery-form{
  margin-top: 2em;
  &__fields{
    @include grid100gap(1em);
  }
}

.delivery-ways{
  margin-top: 2em;
  max-width: 80.3125em;
  &__title{
    font-size: 1.125em;
    line-height: 140%;
    letter-spacing: .01em;
    text-transform: uppercase;
    color: #8c8c8c;
  }
  &__items{
    margin-top: 2em;
    @include grid100gap(1em);
  }
}

.delivery-card{
  padding: 1.5em 1em;
  background-color: $grayVeryWhite;
  &__title,
  &__desc,
  &__price,
  &__time{
    line-height: 140%;
    letter-spacing: .01em;
  }
  &__title{
    font-size: 1.125em;
    text-transform: uppercase;
    font-weight: 500;
  }
  &__desc,
  &__price{
    font-size: 1em;
  }
  &__desc{
    color: $grayDark;
    margin-top: 1em;
    max-width: 70em;
  }
  &__bottom{
    margin-top: 1em;
    display: flex;
    justify-content: space-between;
  }
}

.order-products{
  margin-top: 5em;
  .cart__grid{
    margin-top: 2em;
  }
}

.order-card{
  padding: 1.5em 1em;
  border-radius: .75em;
  background-color: $grayVeryWhite;
  display: grid;
  grid-template-columns: 7.5em calc(100% - 17.5em) 7em;
  gap: 1.5em;
}


@media screen and (max-width: 1400px){
  .order-payment{
    &__nav{
      margin-top: 2em;
      .m-btn{
        padding: 0 6.125em;
      }
    }
  }

  .order-payment,
  .order-delivery,
  .order-contacts{
    margin-top: 5em;
  }

  .order-products{
    margin-top: 3em;
    .cart__grid{
      margin-top: 1em;
    }
    .cart__items{
      gap: .75em;
    }
  }

  .order-card{
    padding: 1em;
    .cart-card__count{
      ._view2{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .order-contacts{
    &__fields{
      margin-top: 1em;
    }
  }

  .delivery-form{
    margin-top: 1em;
  }

  .delivery-ways{
    margin-top: 1.5em;
    &__items{
      margin-top: 1.5em;
    }
  }

  .order-products{
    .cart__grid{
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-payment{
    &__nav{
      margin-top: 1em;
      .m-btn{
        width: 100%;
        padding: 0 1.125em;
      }
    }
  }

  .order-contacts{
    &__fields{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .order-card{
    grid-template-columns: 100%;
    position: relative;
    &__top{
      position: absolute;
      top: 1em;
      left: 6em;
      width: calc(100% - 8em);
    }
    .cart-card__prices,
    .cart-card__title{
      position: static;
      width: 100%;
    }
    .cart-card__prices{
      display: flex;
      align-items: center;
      margin-top: .5em;
    }
    .cart-card__price{
      font-size: 1.125em;
    }
    .cart-card__price2{
      font-size: .75em;
      margin-left: .67em;
    }
    .cart-card__props{
      display: grid;
      grid-template-columns: 1.2fr 1fr 1fr;
      gap: .5em;
    }
    .cart-card__count{
      font-size: .875em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .cart-card__prop{
      display: flex;
      align-items: center;
      .text14{
        margin-right: .25em;
      }
    }
  }
}
